import React, { useEffect, useRef } from 'react';
import gsap from 'gsap';

import octo from './images/octo.png';
import octo2 from './images/octo2.png';
import seattle from './images/seattle.jpg';

const MASK_SIZE = 13;

export const LightBulb = () => {
  const bulbContainer = useRef(null);

  useEffect(() => {
    // set initial value inline to prevent jumping on first mouse move
    gsap.to(
      bulbContainer.current, //
      { '--x': `${50}%`, '--y': `${50}%` }
    );

    gsap
      .timeline({ delay: 1 }) //
      .to(
        bulbContainer.current, //
        { '--maskSize1': `${MASK_SIZE}%`, duration: 0.5, ease: 'back.out(2)' }
      );

    let cursorX: number;
    let cursorY: number;

    const moveMask = () => {
      if (!bulbContainer.current) return;
      const x = cursorX;
      const y = cursorY + window.scrollY;

      const size = y < 750 ? MASK_SIZE : 0;

      const x_rel = Math.round((x / bulbContainer.current.clientWidth) * 100);
      const y_rel = Math.round((y / bulbContainer.current.clientHeight) * 100);

      gsap.to(bulbContainer.current, {
        '--x': `${x_rel}%`,
        '--y': `${y_rel}%`,
        '--maskSize1': `${size}%`,
        duration: 0.3,
        ease: 'sine.out',
      });
    };

    const mouseMoveHandler = ({ clientX, clientY }) => {
      cursorX = clientX;
      cursorY = clientY;
      moveMask();
    };

    const scrollHandler = () => {
      // don't move mask on scroll if coordinates are not set
      if (cursorX !== undefined && cursorY !== undefined) {
        moveMask();
      }
    };

    window.addEventListener('mousemove', mouseMoveHandler);
    window.addEventListener('scroll', scrollHandler);

    return () => {
      window.removeEventListener('mousemove', mouseMoveHandler);
      window.removeEventListener('scroll', scrollHandler);
    };
  }, []);

  return (
    <div
      className="intro content-wrap lightbulb lightbulb-secondary d-none d-xl-block"
      aria-hidden="true"
      ref={bulbContainer}
    >
      <div className="container pseudo-header">
        <div>
          ahn-d-ráy ah-lee-kee-mów-vuhch <br />
          (but you can call me Andrew)
        </div>
      </div>
      <div className="container pb-100">
        <div className="row justify-content-center">
          <div className="col-12 col-lg-10 pb-160 text-white">
            <div className="row mt-90 pt-3">
              <div className="col-12">
                <div className="display-4 text-white">
                  <div
                    data-aos="fade-up"
                    data-aos-delay="50"
                    data-aos-duration="700"
                    data-aos-easing="ease-in-out"
                    data-aos-anchor-placement="top-bottom"
                    style={{ textAlign: 'right' }}
                  >
                    <div className="experience">
                      User
                      <br />
                      Human
                      <br />
                      Digital
                    </div>
                    Experience
                  </div>
                  <div
                    data-aos="fade-up"
                    data-aos-delay="100"
                    data-aos-duration="750"
                    data-aos-easing="ease-in-out"
                    data-aos-anchor-placement="top-bottom"
                    className="justified"
                  >
                    designer based
                  </div>
                  <div
                    data-aos="fade-up"
                    data-aos-delay="150"
                    data-aos-duration="800"
                    data-aos-easing="ease-in-out"
                    data-aos-anchor-placement="top-bottom"
                    style={{ textAlign: 'left' }}
                    className="alt-a"
                  >
                    in&nbsp;Seattle
                    <div className="seattle">
                      <img src={seattle} alt="Seattle" />
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div className="octo">
          <img src={octo} />
        </div>
        <div className="octo-2">
          <img src={octo2} />
        </div>
      </div>
    </div>
  );
};

export default LightBulb;
