import React from 'react';
import InternalLink from '../../Common/InternalLink';

export const Card = (props: {
  href: string;
  fadeDirection: string;
  imgSrc: string;
  title: string;
  subtitle: string;
}) => {
  return (
    <InternalLink
      className="card card-portfolio card-default card-hover-frame"
      to={props.href}
      data-aos={props.fadeDirection}
      data-aos-delay="50"
      data-aos-duration="800"
      data-aos-easing="ease-in-out"
    >
      <span className="card-img">
        <img src={props.imgSrc} alt="" />
      </span>
      <span className="card-body">
        <span className="card-title h4 text-white">{props.title}</span>
        <span className="card-category subtitle text-white">{props.subtitle}</span>
      </span>
    </InternalLink>
  );
};
